.homepage-background {
  background-size: cover;
  background-position: center;
  height: 100vh;
  position: relative;
}

.career {
  background-size: cover;
  background-position: center;
  height: 115vh;
}

body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  scroll-behavior: smooth;
}

.hamburger {
  cursor: pointer;
  font-size: 24px;
  display: none; 
}

nav ul {
  list-style: none;
  display: flex;
  padding: 0;
  margin: 0;
  justify-content: flex-end;
  
  
}

nav ul li a {
  color: white;
  text-decoration: none;
  font-family: 'Cinzel', serif;
  font-size: 22px;
  cursor: pointer;
  transition: color 0.3s ease, border-bottom 0.3s ease;
  position: relative;
  margin-left: -50px;
  margin-right: 100px;
  top: 35px;
  gap: -20px;
}

nav ul li a.active {
  color: orange;
  border-bottom: 2px solid orange;
}

.web {
  color: white;
  font-size: 50px;
  margin-left: -900px; 
  font-family: 'Baron', serif;
  margin-top: 200px;
}

.dev {
  font-family: 'Balgin', serif;
  margin-left: 270px;
}

.programmer {
  position: absolute;
  top: 55px;
  right: 20px;
  width: 500px;
  height: auto;
}

button {
  border-radius: 20px;
  height: 40px;
  width: 130px;
  background-color: #f64929;
  color: white;
  border: none;
  cursor: pointer;
  margin-top: -20px;
  margin-left: -63%;
}

button:hover {
  box-shadow: 0 1px 8px white;
}
@media (max-width: 768px) {
  .hamburger {
    display: block;
  }

  nav ul {
    flex-direction: column;
    gap: 20px;
  }

  .web {
    margin-left: 0;
  }

  button {
    margin-left: 0;
  }
} 
