.footer {
    color: white; 
    padding: 20px 0;
    width: 100%;
    position: relative;
    margin-top: 30px;
    background-color: #171e28; /* Add a background color for better visibility */
}

.footer-container {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}

.footer-section {
    flex: 1;
    margin: 10px;
}

.footer-section h3 {
    color: white;
    text-align: center;
    font-size: 15px;
    margin-right: -800px;
    top: 20px;
    position: relative;
}

.footer-section p {
    text-align: center;
    color: white;
}

.terms ul {
    list-style-type: none;
    display: flex;
    margin: 0; 
}

.terms ul li {
    margin-right: 25px; 
    color: white; 
}

.terms ul li:last-child {
    margin-right: 420px; 
}
