/* about section */
body{
    background-color: #171e28;
}
.whatis{
    color: white;
    text-align: center;
    margin-top: -5px;
    font-family: 'Playfair Display', serif;
}
.think{
    margin-left: -400px;
    margin-top: -250px; 
}
.definition{
    color: white;
    margin-top: 80px;
    font-size: 20px;
}
.definition2{
    color: white;
    font-size: 20px;
}

.definition, .definition2 {
    margin-left: 530px; /* Use the same value for both */
}
.hidden-content {
    opacity: 0;
    transform: translateY(-20px); 
    visibility: hidden; 
    transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out, visibility 0.3s ease-in-out;
}

.hidden-content.show {
    opacity: 1;
    transform: translateY(0); 
    visibility: visible; 
}

.rm_btn {
    position: relative;
    top: -100px;
    margin-left: 60px;
    transition: top 0.5s ease-in-out; 
}

.rm_btn.moved {
    top: -90px; 
}
/* Keyframes for sliding up animation in about section */
@keyframes slideInUp {
    0% {
        opacity: 0;
        transform: translateY(30px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}
.fade-slide {
    opacity: 0;
    transform: translateY(30px);
    transition: opacity 0.3s ease, transform 0.6s ease;
}
.fade-slide-in {
    animation: slideInUp 0.6s ease-out forwards;
}




