/* importance section */
.program {
    left: 30px;
    top: 100px;
    position: relative;
    transform: translateY(50px); 
    opacity: 0; 
    transition: opacity 0.5s ease, transform 0.5s ease; 
}

.gray {
    position: relative;
    top: 20px;
    left: 170px !important;
    transform: translateY(50px); 
    width: 70%;
    height: 270px;
    padding: 20px;
    box-shadow: 0 2px 5px gray;
    background-color: #515964;
    color: white;
    opacity: 0; 
    transition: opacity 0.5s ease, transform 0.5s ease; 
    transform-origin: center; 
}

.show {
    opacity: 1; 
    transform: translateY(0); 
}
.gray h1{
    color: white;
    text-align: center;
    font-size: 26px;
    top: -10px;
    position: relative;
}


/* career part */
.job-container {
    display: flex;
    justify-content: center;
    margin-top: 20px; 
}
.job1, .job2, .job3, .job4{
    font-size: 2rem; 
    width: 30%;
    height: 200px;
    background-color: #2b323c;
    box-shadow: 0 2px 5px gray;
    margin: 10px; 
    text-align: center;
    margin-top: 150px;
    left: 20px;
    position: relative;
}
.job3, .job4{
    text-align: center;
    margin-top: 10px;
    left: 20px;
    position: relative;
}
.career .c{
    font-size: 62px;
    margin: 20px; 
    color: white; 
    margin: 0 auto;
    top: 100px;
    left: -500px;
    position: relative;
}
.design{
    width: 200px;
    height: auto;
    top: -30px;
    position: relative;
}
.analytics{
    width: 150px;
    height: auto;
    top: 10px;
    position: relative;
}
.present{
    width: 250px;
    height: auto;
    top: -30px;
    position: relative;
}
.discuss{
    width: 280px;
    height: auto;
    top: -40px;
    position: relative;
    color: white;
}
.job1 h3, .job2 h3{
    color: white;
    top: -60px;
    position: relative;
    font-size: 20px;
}

.job3 h3{
    color: white;
    font-size: 20px;
    top: -60px;
    position: relative;
}
.job4 h3{
    color: white;
    font-size: 20px;
    top: -90px;
    position: relative;
}
.front{
    width: 200px;
    height: 200px;
    top: -30px;
    position: relative;
}
.c{
    display: none;
}

/* Bounce animation for .think */
@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
        transform: translateY(0);
    }
    30% {
        transform: translateY(-40px); 
    }
    50% {
        transform: translateY(-30px); 
    }
    70% {
        transform: translateY(-20px); 
    }

}

.think {
    animation: bounce 5s ease-in-out infinite; 
}

/* home animation */
@keyframes slideInRight {
    0% {
        opacity: 0;
        transform: translateX(100px);
    }
    100% {
        opacity: 1;
        transform: translateX(0);
    }
}

@keyframes slideInLeft {
    0% {
        opacity: 0;
        transform: translateX(-100px);
    }
    100% {
        opacity: 1;
        transform: translateX(0);
    }
}

.animated.web {
    animation: slideInLeft 1s ease-in-out forwards;
}
.animated.btn {
    animation: slideInLeft 1s ease-in-out forwards;
}

.animated.programmer {
    animation: slideInRight 1s ease-in-out forwards;
}
